.knowmore{
    width: 50%;
    height: auto;
    margin: 50px auto;
    font-family: 'Sorts Mill Goudy', serif;
    font-size: large;
}

.knowmore__img {
    width: 200px;
    height: 180px;
    border-radius:50%;
    object-fit: contain;
}

@media (max-width:939px) {
    .knowmore__nss .knowmore__recreation {
        display: grid;
        grid-template-columns: auto;
    }

    .knowmore__img {
        margin: auto;
        width: 150px;
        height: 100px;
        object-fit: contain;
    }
}