.hamburger{
    width: 2rem;
    height: 2rem;
    display: flex;
    justify-content: space-around;
    flex-flow: column nowrap;
    z-index: 110;
}

.burger{
    width: 2rem;
    height: 0.25rem;
    border-radius: 10px;
    background-color: azure;
    transform-origin: 1px;
    transition: all 0.3s linear;
}