.project {
    width: 50%;
    height: auto;
    margin: 50px auto;
    font-family: 'Sorts Mill Goudy', serif;
    font-size: large;
}

.project__exp {
    margin: 0;
}

.project__expimg {
    background-color: #cce7dd;
    border-radius: 50%;
    width: 200px;
    height: 150px;
    float: left;
    object-fit: contain;
}

.project__expdesc .project__experience{
    word-spacing: 3px;
}

.project__button{
    margin: 30px auto;
    text-align: center;
}

.project__link{
    color: black;
    background-color: #9fcaba;
    padding: 15px;
    font-size: 0.9em;
    text-decoration: none;
    border-radius: 50%;
}

@media (max-width:939px) {
    .project__exp {
        display: grid;
        grid-template-columns: auto;
    }

    .project__expimg {
        margin: auto;
        width: 150px;
        height: 100px;
        object-fit: contain;
    }

    .project__link{
        color: black;
        background-color: #9fcaba;
        padding: 8px;
        font-size: 0.5em;
        text-decoration: none;
        border-radius: 50%;
    }
}